            cols="12"
            md="4"
            sm="6"
          >
            <v-dialog transition="dialog-bottom-transition" width="auto">
              <template v-slot:activator="{ on, attrs }">
                <v-card
                  flat
                  class="mx-auto ma-2"
                  height="auto"
                  width="100%"
                  v-bind="attrs"
                  v-on="on"
                >
                  <v-hover>
                    <v-card
                      flat
                      :class="`${hover ? 'imgover' : 'imgleave'}`"
                      slot-scope="{ hover }"
                    >
                      <v-img
                        :src="url + product.images[0]"
                        class="mt-3 mb-3 rounded-xl"
                      ></v-img>
                    </v-card>
                  </v-hover>

                  <v-card-title align="center" justify="center">{{
                    product.sku.name
                  }}</v-card-title>
                </v-card>
              </template>

              <template v-slot:default="dialog">
                <v-card flat class="rounded-xl pa-10">
                  <v-row no-gutters align="center" justify="center">
                    <v-col sm="12" md="6">
                      <v-img
                        :src="url + product.images[0]"
                        class="mr-6 rounded-r-xl"
                      ></v-img>
                    </v-col>

                    <v-col sm="12" md="6">
                      <div class="text-uppercase page-title mt-10">
                        {{ product.sku.name }}
                      </div>
                      <div class="mt-2">
                        <h1>{{ product.category.name }}</h1>
                      </div>
                      <div class="mt-2">
                        <p>{{ product.category.name }}</p>
                      </div>
                      <div>
                        {{ product.sku.description }}
                      </div>
                      <!-- <v-row class="mt-2">
                        <v-col cols="6">
                          <span style="display: block">Location</span>
                          <span>{{ product.location }}</span>
                        </v-col>
                        <v-col cols="6">
                          <span style="display: block">Timings</span>
                          <span>{{ product.timing }}</span>
                        </v-col>
                      </v-row> -->
                      <v-card-actions class="mt-5">
                        <v-btn
                          to="http://godutyfree.in"
                          block
                          rounded
                          dark
                          class="gradient-button"
                          @click="gtrack()"
                        >
                          Website
                        </v-btn>

                        <!-- <v-btn
                          elevation="0"
                          icon
                          style="background-color: #e8effe"
                        >
                          <v-icon color="#0072FF"> mdi-phone </v-icon>
                        </v-btn>
                        <v-btn
                          elevation="0"
                          icon
                          style="background-color: #e8effe"
                        >
                          <v-icon color="#0072FF"> mdi-email </v-icon>
                        </v-btn> -->
                      </v-card-actions>
                    </v-col>
                  </v-row>
                </v-card>
              </template>
            </v-dialog>
          </v-col>
        </v-row>

        <div class="text-center">
          <v-pagination
            v-model="page"
            :length="15"
            :total-visible="7"
            :products="products.length"
            :per-page="perPage"
            @input="getPage"
          ></v-pagination>

          <!-- <pagination :length="15" :total-visible="7" :products="products.length" v-model="page" :per-page="perPage" @paginate="getPage">

  </pagination>  -->
        </div>
      </v-container>
