</template>
<script>
import rightStickIcon from "../../components/rightStickIcon";
import axios from "axios";

export default {
  components: {
    rightStickIcon,
  },

  data() {
    return {
      page: 1,
      perPage: 21,
      products: [],
      productsLength: 0,

      items: [
        {
          src: require("../../views/attheairport/images/dutyfree-banner.jpg"),
          title:
            "Discover Premium Brands at Irresistible Prices Before You Fly!",
        },
        {
          src: require("../../views/attheairport/images/dutyfree-banner-2.jpg"),
          title:
            "Indulge in a Tax-Free Shopping Extravaganza at the Duty-Free Shops",
        },
      ],
      // products: [
      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },
      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },

      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },
      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },

      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },
      //   {
      //     src: require("../../views/attheairport/images/shopping/Ayurveda.jpg"),
      //     title: "After Eight Variety Sharing Bag 550 Gm",
      //     name: "COLORBAR",
      //     category: "Shopping",
      //     location: "Location",
      //     detailcontent: "T 1 Departures",
      //     timing: "",
      //   },
      // ],
      url: process.env.VUE_APP_AIRLlINE_URL,
    };
  },
  mounted() {
    console.log("product_id", this.$store.getters.getProductId);
    this.getproduct(this.$store.getters.getProductId);
  },

  methods: {
    async getproduct(id, page = 1, limit = 21) {
      this.products = [];
      let product = await axios.post(
        `${process.env.VUE_APP_AIRLlINE_URL}/user/product`,
        {
          page: page,
          limit: limit,
          search: "",
          sortKey: "name",
          sortDirection: "ASC",
          filters: {
            materialGroup: id,
          },
        }
      );
      // console.log("product ", product);

      if (product.data.success) {
        console.log(product.data.data.docs);
        this.products = product.data.data.docs;
      }
    },
    getwebsite() {
      window.location.href = "godutyfree.in";
    },

    getPage(page) {
      // we simulate an api call that fetch the records from a backend
      console.log("Get Page", page);
      console.log("Get Page this.page", this.page);
      console.log("Get Page", this.perPage);
      if (page >= this.page) {
        this.perPage += 20;
      } else this.perPage -= 20;
      this.products = [];
      const startIndex = this.perPage * (page - 1) + 1;
      const endIndex = startIndex + this.perPage - 1;
      for (let i = startIndex; i <= endIndex; i++) {
        this.products.push(`Item ${i}`);
      }

      this.getproduct(this.$store.getters.getProductId, this.page, 21);
      // this.page = page;
    },
    getProductsLength() {
      // here we simulate an api call that returns the records length
      this.productsLength = 500;
    },
  },

  created() {
    this.getProductsLength();
    this.getPage(this.page);
  },

  computed: {
    columns() {
      if (this.$vuetify.breakpoint.xl) {
        return 4;
      }

      if (this.$vuetify.breakpoint.lg) {
        return 3;
      }

      if (this.$vuetify.breakpoint.md) {
        return 2;
      }

      return 1;
    },
  },
};
</script>
